<template>
    <div class="shilde">
        <div>
            <h3>功能分配</h3><h5>为角色<span style="color:#ff0000"><span v-if="checkData[0]">{{checkData[0].rolename}}</span></span>分配功能</h5>
        <div>
             <table class="table table-bordered table-striped table-hover">
          <thead>
            <tr>
              <th><input type="checkbox" name="" id=""></th>
              <th v-for=" (title,index) in page.titleData" :key="index" v-text="title"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(datas,index) in page.datas" :key="index">
              <td><input type="checkbox" name="" id="" @change="updateCheckData(datas,$event)" ></td>
              <td v-for="(data,index) in datas" :key="index" v-html="data"></td>
            </tr>
          </tbody>
        </table>
         <div>
              <button class="btn btn-info" @click="updatejueFun">提交</button> <button class="btn btn-warning" @click="closeupdateFun">返回</button>
          </div>
        </div>
    </div>
    </div>
</template>
<script>
export default {
    data:()=>{
        return {
            page:{
                datas:[],
                titleData:["菜单名","类型","描述"]
            },
            data:{
                roleid:"",
                addData:[],
                deleteData:[]
            }
        }
    },
    props:{
        tablefunall:Boolean
    },
    computed:{
        checkData(){
            if(this.$store.getters.GETUSERTABLEPAGECHECKDATA){
                return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
            }
        }
    },
    methods:{
        updatejueFun(){
            this.data.roleid=this.checkData[0].id;
            this.$api.http.opreateHttp(this,"updataDistributionFunction.action",this.data,"修改功能");
        },
        closeupdateFun(){
            var vm=this;
            vm.$api.opreate.emitCall("tablefunCall",false,vm);
        },
        // 获取功能
        loginFunData(){
            var vm=this;
            vm.$axios.post(vm.HOST+"updataDistributionFunction.action").then(function(success){
                let data=success.data;
                console.log(data);
                if(data.code=="0"){
                    vm.page.datas=data.data;
                }else if(data.code=="500"){
                    vm.$layer.msg("功能获取失败",{icon:5});
                }
            }).catch(function(){console.log(error);vm.$layer.msg('网络错误，请检查网络是否畅通',{icon:5});})
        },
        updateCheckData(data,e){
            var vm=this;
            var data=data;
            var checkdata=vm.data;
            var checkad=e.target.checked;
            if(checkad){
                if(!checkdata.addData.indexOf(data.id)>-1){
                    if(!checkdata.deleteData.indexOf(data.id)>-1){
                        checkdata.deleteData.splice(data.id,1);
                    }
                    checkdata.addData.push(data.id);
                }
            }else{
                if(!checkdata.deleteData.indexOf(data.id)>-1){
                     if(!checkdata.addData.indexOf(data.id)>-1){
                        checkdata.addData.splice(data.id,1);
                    }
                    checkdata.deleteData.push(data.id);
                }
            }
        }
    }
}
</script>

<style>
.shilde{
    position: absolute;
    top: 0;
    left:-1rem;
    padding: 2rem;
    width: 100%;
    height: 100%;
    background: #fff;
    z-index: 1000;
}
</style>




